import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Channel = () => import('components/channel/channel')
const Sort = () => import('components/sort/sort')
const Rank = () => import('components/rank/rank')
const List = () => import('components/list/list')
const Book = () => import('components/book/book')
const Catalog = () => import('components/catalog/catalog')
const Read = () => import('components/read/read')
const ReadTest = () => import('components/test/test')

export default new Router({
    mode: 'hash',
    routes: [
        {
            path: '/',
            redirect: '/channel'
        },
        {
            path: '/channel',
            component: Channel
        },
        {
            path: '/sort',
            component: Sort
        },
        {
            path: '/rank',
            component: Rank
        },
        {
            path: '/list/:id',
            component: List
        },
        {
            path: '/book/:id',
            component: Book
        },
        {
            path: '/catalog/:id',
            component: Catalog
        },
        {
            path: '/read/:id/:chapter',
            component: Read,
            name: 'Read'
        },
        {
            path: '/test',
            component: ReadTest,
            name: 'ReadTest'
        }
    ]
})